<template>
  <div class="header-container">
    <div class="l-content">

      <div style="display: flex; align-items: center" @click="jumpMain()">
        <img style="width: 32px; height: 32px; margin: 16px" src="../assets/logo.png"/>
        <h3>AppLocalize</h3>
      </div>

    </div>
    <div class="r-content">
      <el-dropdown @command="handleDropdown">
        <span class="el-dropdown-link">{{userName()}}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="config">Config</el-dropdown-item>
          <el-dropdown-item command="logout">Logout</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>

    <el-dialog title="User Configuration" :visible.sync="isShowConfigDlg">
      <el-form :model="configurationFrom">
        <el-form-item label="Google Service Account Json">
          <el-input type="textarea" resize="none"  v-model="configurationFrom.googleJson"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="isShowConfigDlg = false">Cancel</el-button>
        <el-button :loading="isConfigLoading" type="primary" @click="updateConfig">Update</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
import Cookie from 'js-cookie'
import {configProject} from "@/api";
export default {
  data() {
    return {
      isShowConfigDlg: false,
      isConfigLoading: false,
      configurationFrom: {
        googleJson: ''
      }
    }
  },
  methods: {
    handMenu() {
      this.$store.commit('collapseMenu')
    },
    jumpMain() {
      this.$router.push('/')
    },
    handleDropdown(commaand) {
      if (commaand == "logout") {
        Cookie.remove("token");
        this.$router.push('/')
      } else if (commaand == "config") {
        this.isShowConfigDlg = true
        this.configurationFrom.googleJson = null
      }
    },
    updateConfig() {
      this.isConfigLoading = true

      configProject(this.configurationFrom).then((data) => {
        this.isConfigLoading = false
        if (data.code == 200) {
          this.isShowConfigDlg = false
        }
      })
    },
    userName() {
      return Cookie.get("name")
    }
  }
}
</script>

<style lang="less" scoped>
.header-container {
  padding: 0 20px;
  height: 60px;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.el-dropdown-link {
  cursor: pointer;
  color: black;
}

.el-icon-arrow-down {
  font-size: 12px;
}
</style>