<template>

  <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose"
           :collapse="isCollapse"
           :default-active="menuActiveIndex"
           background-color="#092037" text-color="#FFFFFF">

    <div style="display: flex; align-items: center" @click="clickMain()">
      <img style="width: 32px; height: 32px; margin: 16px" src="../assets/logo.png"/>
      <h3 style="margin: 0">{{isCollapse ? "": "AppLocalize"}}</h3>
    </div>

    <el-menu-item @click="clickMenu(item)" v-for="item in noChildren" :key="item.name" :index="item.name">
      <i :class="item.icon"></i>
      <span slot="title">{{item.name}}</span>
    </el-menu-item>

  </el-menu>
</template>

<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 254px;
  min-height: 400px;
}

.el-menu {
  height: 100vh;
  border-right: none;
  h3 {
    color: #FFF;
    text-align: center;
    line-height: 48px;
  }
}
</style>

<script>
export default {
  data() {
    return {
      menuActiveIndex: 'Dashboard',
      menuData: [
        {
          path: '/project/:id/dashboard',
          name: 'Dashboard',
          icon: 'el-icon-s-data',
          url: ''
        },
        {
          path: '/project/:id/strings',
          name: 'Strings',
          icon: 'el-icon-finished',
          url: ''
        },
        {
          path: '/project/:id/submission',
          name: 'Submission',
          icon: 'el-icon-s-custom',
          url: ''
        },
        {
          path: '/project/:id/listing',
          name: 'Store Listing',
          icon: 'el-icon-s-shop',
          url: ''
        },
        {
          path: '/project/:id/screenshots',
          name: 'Screenshots',
          icon: 'el-icon-s-shop',
          url: ''
        },
      ]
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath)
    },
    clickMain() {
      this.$router.push("/")
    },
    clickMenu(item) {
      const replaced = item.path.replace(":id", this.$route.params.id)
      console.log(this.$route.path, item.path, replaced)
      if (this.$route.path != replaced) {
        this.$router.push(replaced)
        this.$store.commit('updateTabName', item.name)
      }
    }
  },
  mounted() {
    this.menuData.forEach(item => {
      const replaced = this.$route.path.replace(this.$route.params.id, ":id")
      console.log(replaced + " sss " + item.path)
      if (item.path == replaced) {
        this.menuActiveIndex = item.name
        this.$store.commit('updateTabName', item.name)
      }
    })
  },
  computed: {
    noChildren() {
      return this.menuData.filter(item => true)
    },
    isCollapse() {
      return this.$store.state.tab.isCollapse
    },
  }
}
</script>